@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400;900&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
   */
  font-family: 'Noto Sans JP', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
  /* font-family: 'Noto Sans JP', sans-serif; */
  
}
*{
    margin: 0;
}
.header{
    background-color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
  
}


.header__logo {
    width: 100px;
    height: 40px;
    margin-left: 60px ;
    margin-right: 40px;
    margin-top: 18px;
    cursor: pointer;

}

.header__menu {
    margin-left: 70px;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10px;
}
.header__title {
    margin-top: 4px;
    padding: 12px;
    font-size: 14px;
    color: #111111;
    text-decoration: none;
    text-transform: none;
    /* justify-content: space-between; */
}

.header__pro {
    text-decoration: none;
}

.header__searchIcon, 
.header__cameraIcon {
   
    color: rgb(58, 55, 55);
    position: absolute;
    margin-top: 8px;
}
.header__searchIcon {
    margin-left: 15px;
}
.header__cameraIcon {
    -webkit-justify-content: flex-start !important;
            justify-content: flex-start !important; 
    margin-left: 480px !important;
}

.header__search {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: .6 1;
            flex: .6 1;
    margin-left: 25px;
    margin-top: 10px;
}
.header__searchInput {
    height: 20px;
    padding: 10px;
    width: 100%;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 20px;
    border: none;
    background-color: #F2F3F4;
    padding-left: 42px;
    font-size: 16px;
}

.header__nav {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-flex: .3 1;
            flex: .3 1;
    margin-left: 40px;
    margin-right: 0px;
    
}
.header__option {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: #111111;
    margin: 25px;
    /* width: 80px; */
    flex-direction: column;

}
.header__optionProfile {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: #111111;
    margin-top: 15px;
    margin-right: 30px; 
    width: 50px;
    margin-left: 15px;
    -webkit-align-items: center;
            align-items: center;
    
}
.header__optionProfile > span {
    font-size: 9px;
    text-align: center;
    width: 50px;
    font-weight: 700;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    color: #023766;
}
.header__link{
    text-decoration: none;
    cursor: pointer;
    /* display: flex; */
}
.header__optionLineTwo {
    font-size: 13px;
    font-weight: 800;
    /* color: red; */
}
.header__optionBasket {
    display: -webkit-flex;
    display: flex;
    margin-top: 22px;
    margin-left: 10px;
    color: #111111;
    -webkit-align-items: center;
            align-items: center;
}

.header__basektCount{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 5px;
}

@media (max-width: 1200px) {
    .header__right {
        overflow: hidden;
        display: none !important;
    }
    
    .header__searchInput {
        width: 100%;
    }
}

@media (max-width: 884px) {
    .header {
        margin-top: 0px;
    }
    .header__title {
        display: none;
    }
    .header__search  {
        -webkit-flex: 1 1;
                flex: 1 1;
    }
    .header__searchInput {
        width: 100%;
    }
    .header__nav {
        
        -webkit-flex: .1 1;
        
                flex: .1 1;
    }
    .header__option {
      
        margin: 23px;
    
    }
  
    .header__logo {
        margin-left: 30px;
        width: 70px;
    }
    
}
@media (max-width:670px){
    .header__title {
        display: none;
    }
    .header__search  {
        display: none;
        /* flex-direction: column; */
    }
    
    .header__nav {
        
        -webkit-flex: .1 1;
        
                flex: .1 1;
    }
    .header__option {
        /* margin-top: 23px; */
        padding-right: 0px;
        margin: 22px;
       
    
    }
  
   
    .header__logo {
        margin-left: 30px;
        width: 60px;
        margin-top: 10px;
    }
}
.products {
    margin: 80px 100px;
    /* height: 70vh; */
}


.products__banner {
    margin-bottom: 30px;
    font-size: 36px;
}

.products__paraph {
    margin-top: 35px;
    width: 460px;
    margin-bottom: 20px;
}
.products__paraphTitle {
    font-size: 22px;
    margin-bottom: 10px;
}

.products__paraphDetail {
    font-size: 14px;
    color: #474646;
    line-height: 25px;
    -webkit-justify-content: center;
            justify-content: center;
}

.products__recommd {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 50px;
}
.products__recommd > button {
    background-color: #6EB7FF ;
    height: 40px;
    width: 15%;
    color: white;
    font-size: 13px;
    font-weight: 900;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}

.products__row {
    display: -webkit-flex;
    display: flex;
    z-index: 1;
    margin-left: 25px;
    margin-right: 15px;
    margin-bottom: 50px;
}


.products__new {
    /* display: flex; */
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 120px;
}
.products__new > p{
    font-size: 16px;
    color: #474646;
    margin-bottom: 30px;  
    -webkit-justify-content: center;  
            justify-content: center;
}

.products__gridRoot {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    overflow: hidden;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}
.products__gridTile {
    width: 100%;
    /* height: 400px; */
}



.products__gridList {
    /* width: 500px; */
    display: -webkit-flex;
    display: flex;
    -webkit-flex-grow: 2;
            flex-grow: 2;
 
    /* max-height: 100%; */
  
}

.products__gridImg {
    object-fit: contain;
}
@media (max-width:670px) {
    .products {
        margin: 20px 0px;
    }
    .products__banner {
        font-size: 24px;
        margin-left: 30px;
    }
    .products__paraph {
        margin-left: 30px;
        width: 400px;
    }
    .products__paraphTitle {
        font-size: 18px;
    }
    .products__row {
        /* display: inline-flex; */
        margin-left: 5px;
        margin-right: 5px;
    }
    .products__recommd {
        margin-left: 30px;
    }
    .products__recommd > button {
        width: 25%;
        font-size: 10px;
        font-weight: 800;
        margin-right: 15px;
    }
    .products__gridTile {
        width: 100%;
       
    }
}
.product {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 80px;
    max-width: 200px;
    max-height: 400px;
    -webkit-align-items: center;
            align-items: center;
    /* background-color: yellow; */
    /* margin: 10px; */
    margin-left: 15px;
    z-index: 1;
    padding: 15px;
}

.product__info {
    height: 100px;
    margin-top: 30px;
    margin-bottom: 5px;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
}

.product__title {
    font-weight: bold;
    font-size: 20px;
}
.product__price {
    margin-top: 5px;
    font-size: 18px;
}

.product__price > small {
    font-size: 18px;
}
.product__rating {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    
}

.product > img {
    width: 100%;
    max-height: 250px;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    background-color: #E5E6E7 ;
    height: 40px;
    width: 100%;
    color: #111111;
    font-size: 14px;
    font-weight: 700;
    border: none;
    border-radius: 30px;
    cursor: pointer;
}
.product > button:hover {
    background-color: #0050A0;
    color: white;
    border: none;

}

@media (max-width:670px) {
    .product {
        padding: 0;
    }
.product > button {
    font-size: 12px;
    font-weight: 700;
}
.product > img {
    width: 100%;
    max-height: 100px;
    object-fit: contain;
    margin-bottom: 15px;
}
}

.footer {
    margin-top: 50px;
    background-color: #E5E7E9;
    position: absolute;
    width: 100%;
}

.footer__top {
    padding: 50px 50px;
    display: -webkit-flex;
    display: flex;
}

.footer__info {
    width: 250px;
   margin: 30px 40px;

}
.footer__info > h3 {
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 20px;
}
.footer__info > p {
    font-size: 14px;
    line-height: 25px;
    color: #1C2833;
    -webkit-justify-content: center;
            justify-content: center;
}

.footer__infoLink {
    font-size: 11px;
    font-weight: bold;
    padding: 10px;
    text-decoration: none;
    color: #484a4b;
}

.footer__infoList > ul {
    display: -webkit-flex;
    display: flex;
    list-style: none;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 100%;
    margin: 40px 20px;
    /* padding-left: 100px; */
   
    -webkit-justify-content: space-between;
   
            justify-content: space-between;
}
.footer__infoLi {
    padding-right: 40px;
    -webkit-flex: 1 1;
            flex: 1 1;
    /* width: 166px; */
}

.footer__infoListTitle {
    font-size: 14px;
    margin-bottom: 20px;
    /* margin-left: 20px; */
}
.footer__infoLinks > a {
    font-size: 14px;
    display: -webkit-flex;
    display: flex;
    margin-top: 20px;
    color: #1C2833;
    cursor: pointer;
}
.footer__infoLinks > a:hover {
    text-decoration: underline;
}

.footer__social {
    padding: 50px 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.footer__links {
    margin: 20px;
    display: -webkit-flex;
    display: flex;

   
}

.footer__socialIcons {
    background-color: transparent !important;
  width: 40px;
  height: 40px;
  border: 1px solid #B2BABB;
  /* padding-top: 10px; */
    text-align:center;
    border-radius: 50%;
    font-size: 28px;
    /* padding: 5px; */
    margin-left: 20px;
}

.footer__icon {
    color: #1C2833;
}

.footer__buttons {
    display: -webkit-flex;
    display: flex;
}
.footer__buttons > button {
    /* padding: 10px; */
    /* margin-top: 20px;
    margin-right: 15px; */
    /* text-align: center; */
    height: 40px;
    width: 150px;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin: 20px;
    display: -webkit-flex;
    display: flex;
    border-radius: 50px;
    border: 1px solid #484a4b;
    background-color: transparent !important;

}
.button__icon {
    padding-right: 5px;
    padding-top: 5px;
    color: #1C2833;

}
.button__name {
    padding-top: 10px;
    font-size: 12px;
    font-weight: 700;
    color: #1C2833;
    line-height: 18px;
}

.footer__line {
    /* background-color: tomato; */
    margin: 20px 50px;
    width: 90%;
}

.footer__bottom {
    display: -webkit-flex;
    display: flex;
    margin: 20px 30px;
}

.footer__bottom > p {
    font-size: 12px;
    margin-right: 40px;
    margin-left: 20px;
    color: #484a4b;
}
.footer__bottomTags {
    display: -webkit-flex;
    display: flex;
    /* margin-left: 20px; */
    /* justify-content: space-between; */
}

.footer__bottomTags > a {
    font-size: 12px;
    -webkit-flex-grow: 2;
            flex-grow: 2;
    padding-left: 40px;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #1C2833;
    cursor: pointer;
}

.footer__bottomTags > a:hover {
    text-decoration: underline ;
}


@media (max-width: 670px)  {
    .footer {
      /* flex-wrap: wrap; */
      -webkit-flex-direction: column;
              flex-direction: column;
      width: 100% auto;
      object-fit: contain;
      background-size: cover;
    }

    .footer__infoList {
        display: none;
    }
    .footer__top {
        padding: 20px;
      
    }
    .footer__info {
        width: 400px;
       margin:0px 0px;
    
    }
    .footer__social {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        padding: 0px 0px;
    }
    .footer__buttons {
        margin-left: 20px;
    }
    .footer__line {
        display: none;
    }

    .footer__bottom {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }
    .footer__bottomTags {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        padding-left: 0px;
        margin-top: 20px;
        margin-left: 0px;
        
    }
    .footer__bottom > a {
        padding-left: 0px;
        
        margin-left: 0px;
    }
    .footer__infoList > ul  {
        margin: 0px 0px;
    }
}
